.card
  overflow: visible !important

.custom-control-label
  font-size: 16px

@media (max-width: 768px)
  .mobile-full-width
    width: 100%
    margin-top: .25em
    margin-bottom: .4em

.section-title
  padding-bottom: 5px
  border-bottom: 1px solid rgb(var(--color-text-primary))

.form-label
  font-size: 18px

section.danger
  .section-title
    color: rgb(var(--color-danger))
    border-color: rgb(var(--color-danger))
